* {
margin: 0;
padding: 0;
box-sizing: border-box;
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

html, body {
    min-height: 100%;
    overflow-y: auto;
}
::-webkit-scrollbar {
    display: none;
  }

body {
background: #0a0a20;
color: #ffffff;
}

.navbar {
padding: 1rem 2rem;
background: rgba(10, 10, 32, 0.95);
display: flex;
justify-content: space-between;
align-items: center;
width: 100vw;
top: 0;
z-index: 10;
backdrop-filter: blur(10px);
}

.logo-container {
display: flex;
align-items: center;
gap: 0.5rem;
text-decoration: none;
}

.cow {
width: 50px;
height: 50px;
object-fit: contain;
}

.logo-text {
color: #ffffff;
font-size: 1.5rem;
font-weight: bold;
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.nav-links {
display: flex;
gap: 2rem;
}

.nav-links a {
color: #ffffff;
text-decoration: none;
font-weight: 500;
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.titleing {
height: 80vh;
width:100vw;
background: linear-gradient(rgba(10, 10, 32, 0), rgba(10, 10, 32, 0.1)),
            url('https://cdn.glitch.global/2cc97dfb-e0e2-411d-bca2-fc582838a41b/image%2012.png?v=1731785995118');
background-size: cover;
background-position: center;
display: flex;
align-items: center;
justify-content: center;
text-align: center;
position: relative;
flex-direction: column;
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.titleing::before {
content: '';
position: absolute;
top: 0;
left: 0;
right: 0;
bottom: 0;
background: radial-gradient(circle at center, transparent 0%, rgba(10, 10, 32, 0.8) 100%);
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.titleing h1 {
font-size: 4.5rem;
margin-bottom: 1rem;
position: relative;
z-index: 1;
text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.titleing h2 {
font-size: 1.5rem;
position: relative;
z-index: 1;
text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.features {
display: grid;
grid-template-columns: repeat(2, 1fr);
gap: 2rem;
padding: 4rem 2rem;
max-width: 1400px;
margin: 0 auto;
display: flex;
}

.feature-card {
background: rgba(255, 255, 255, 0.05);
border-radius: 1rem;
padding: 2rem;
text-align: center;
transition: transform 0.3s ease;
cursor: pointer;
backdrop-filter: blur(10px);
border: 1px solid rgba(255, 255, 255, 0.1);
}

.feature-card:hover {
transform: translateY(-5px);
background: rgba(255, 255, 255, 0.08);
}

.feature-card img {
width: 100%;
height: 200px;
object-fit: cover;
border-radius: 0.5rem;
margin-bottom: 1rem;
}

.feature-card h3 {
font-size: 1.25rem;
margin-bottom: 1rem;
color: #ffffff;
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.feature-card p {
color: rgba(255, 255, 255, 0.8);
line-height: 1.5;
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.glow {
position: absolute;
width: 200px;
height: 200px;
background: radial-gradient(circle at center, rgba(5, 103, 46, 0.654), transparent 70%);
border-radius: 50%;
pointer-events: none;
z-index: 0;
}

@keyframes float {
0% { transform: translateY(0px); }
50% { transform: translateY(-20px); }
100% { transform: translateY(0px); }
}

.floating {
animation: float 6s ease-in-out infinite;
}

.startbutton {
position: relative;
z-index: 2;
padding: 1rem 2.5rem;
font-size: 1.2rem;
font-weight: 600;
color: #fff;
background: linear-gradient(45deg, #00a8ff, #00ff95);
border: none;
border-radius: 30px;
cursor: pointer;
transition: all 0.3s ease;
text-decoration: none;
margin-top: 2rem;
display: inline-block;
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.startbutton::before {
content: '';
position: absolute;
top: -2px;
left: -2px;
right: -2px;
bottom: -2px;
background: linear-gradient(45deg, #00a8ff, #00ff95, #00a8ff);
border-radius: 30px;
z-index: -1;
opacity: 0;
transition: opacity 0.3s ease;
}

.startbutton:hover {
transform: translateY(-3px);
box-shadow: 0 0 20px rgba(0, 168, 255, 0.5);
}

.startbutton:hover::before {
opacity: 1;
animation: rotate 2s linear infinite;
}

@keyframes rotate {
0% {
    filter: hue-rotate(0deg);
}
100% {
    filter: hue-rotate(360deg);
}
}

.titleing-content {
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
text-align: center;
position: relative;
z-index: 1;
}

.titleing h1 {
margin-bottom: 1rem;
font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

@keyframes pulse {
0% {
    box-shadow: 0 0 0 0 rgba(0, 168, 255, 0.4);
}
70% {
    box-shadow: 0 0 0 10px rgba(0, 168, 255, 0);
}
100% {
    box-shadow: 0 0 0 0 rgba(0, 168, 255, 0);
}
}

.pulse {
animation: pulse 2s infinite;
}

.resources {
background-color: #2d3436;
}

.mapping {
background-color: #4a90e2;
}

.cursor {
cursor:pointer;
}
